@import url(https://fonts.googleapis.com/css2?family=Cormorant+SC:wght@300&family=Courgette&family=MonteCarlo&family=Permanent+Marker&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


ul{
    display: flex;
    justify-content:flex-end;
    color: #ff0000;
    list-style-type: none;
    -webkit-padding-start: 0;
            padding-inline-start: 0;
    margin: 0;
    
   
}
li{
    margin: 10px;
    font-size: 0.8em;
   
}
.link{
   text-decoration: none;
    color:#FF6060;
    font-weight: 500;
   
}

.activated{
    text-decoration: underline;
}

a{
    text-decoration: none;
}
@media screen and (max-width: 699px){
    nav li{
        font-size: 12px;
    }
}
.container{
    display: flex;
    justify-content: space-between;
    align-self: baseline;
    margin: 20px;
    width: 90%;
}

.logo{
    height: 50px;
    width: 180px;
}
@media screen and (max-width: 699px){

    .logo{
        height: 30px;
        width: 70px;
    }
    .container{
        margin: 20px 30px;
    }
    
}
.footer-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: black;
    color: white;
    justify-content: space-between;
    align-items: center;
    padding-top: 25px;
    height: 100px;
}
.footer-container img{
    width: 100px;
}
.footer-container p{
    color: white;
}
span{
    background-color: black;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: 1px solid white;
}

body{
    margin: 0;
    margin: 0 50px;
}
.container{
    margin: 20px 50px;
}
.desktop{
    display: block;
}
.mobile{
    display: none;
}


.about-block{
    display: flex;
    flex-direction: column;
    background-color: #FF6060;
    margin: 10px 80px;
    color: #F6F6F6;
}

.title{
    display: flex;
  justify-content: flex-end;
}
.expanded{
    visibility: collapse;
    height: 0;
}
h1{
   background-color: #FF6060;
   color: white;
   width: 100%;
}


.vector{
height: 30px;
width: 30px;
position: absolute;
rotate: 180deg;
margin-left: 0px;
align-self: self-end;
cursor: pointer;
margin-left: 20px;
}
.texte{
    height: -webkit-max-content;
    height: max-content;
    background-color: #F6F6F6;
    font-size: 1em;
    padding: 5px 0;
}
@media screen and (max-width: 699px){
    
    body{
        margin: 0 10px;
    }
    .container{
        margin: 20px 10px;
    }
    .Mobile{
        display: block;
    }
    
    .about-block{
        margin: 0 30px;
    }

    .texte{
        font-size: 12px;
    }
}

.cover{
    display: flex;
    margin: 20px 0;
}

.imgAccueil{
    width: 100%;
    height: 180px;
    -webkit-filter: contrast(0.5);
            filter: contrast(0.5);
    border-radius: 15px;
   
}
.cover p{
    font-family: 'montserrat';
    position: absolute;
    color: white;
    font-family: 'montserrat';
    position: absolute;
    font-size: 30px;
    z-index: 6;
}
.listCard{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content:center;
   max-width: 1200px;
   background-color:#F6F6F6;
   margin: 0 30px;
   border-radius: 15px;
    
}
.card{
    display: flex;
    flex-direction: column-reverse;
    height: 240px;
    width: 260px;
    margin: 10px 20px;
    border-radius: 15px;
    background: rgb(255,96,96);
    background: linear-gradient(180deg, rgba(255,96,96,1) 35%, rgba(13,9,12,0.5788690476190477) 100%);  
}
.card img{
    width: 100%;
    height: 100%;
    border-radius: 15px;
    object-fit: cover;
}
.card b{
    position: absolute;
    margin-bottom: 20px;
    color: white;
    font-size: 13px;
    width: 20%;
    display: flex;
    flex-wrap: wrap;
    

}

@media screen and (max-width: 699px){

    .imgAccueil{
        height: 100px;
    
    }
    .listCard{
        background-color: white;
        margin:0 0;
    }
    .card{
        height: 100%;
         width: 100%;
         margin: 10px 0px;
    }
    .cover p{
        font-size: 16px;
    width: 50%;
    }
}
*{
    font-family: 'montserrat';
    color: #FF6060;
}

img{
    width: 100%;
    height: 100%;
}
.content{
    display: flex;
    justify-content: space-between;
    margin: 20px;
}
.content p{
    font-size: 18px;
    color: #FF6060;
   

}
.content b{
    color: #FF6060;
    font-size: 36px;
    background-color: white;
}
.content.img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
   background-color: #C4C4C4;
}
.bloc-right{
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-between;
}
.host{
    display: flex;
}
.host img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.host p{
   
    width: 80px;
    color: #FF6060;
    text-align: -webkit-right;
    margin-right: 10px;
}
.tags{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}
.tags li{
    justify-content: flex-start;
    color:white;
    background-color:#FF6060;
    border-radius: 10px;
    width: 115px;
    padding: 5px 5px;
    text-align: center;

        
}
.description{
    display:flex ;
}

.text div, .equipements ul{
    background-color: #F6F6F6;
}
.equipements,.text{
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: 20px;
}
.active-star{
    fill: #FF6060;
}

h2{
    padding: 15px 20px;
    background-color: #FF6060;
    border-radius: 10px;
    color: white;
    -webkit-margin-before: 0;
            margin-block-start: 0;
    -webkit-margin-after: 0;
            margin-block-end: 0;
}



svg{
    width: 30px;
    height: 30px;
   fill:#C4C4C4;
}

.equipements ul {
    display: flex;
    flex-direction: column;
}
@media screen and (max-width:500px) {

    .host img{
        width: 32px;
        height: 32px;
    }
}

@media screen and (max-width: 890px){
   
    .slider{
        margin: 0 20px;
    }
    .content{
        margin: 0;
        flex-direction: column;
    }
    .content b{
        font-size: 18px;
    }
    .content p{
        font-size: 14px;
    
    }
   
    .bloc-right{
        flex-direction: row;
    justify-content: space-between;
    }
    .tags ul{
        justify-content: flex-start;
        width: 100%;
    }
    .tags li{
        width: 115px;
        padding: 5px 5px;
    }
 

    .NbRating{
        display: flex;
    align-items: center;
    }

.description{
    flex-direction: column;
}
.equipements, .text {
    width: 100%;
    margin: 20px 0;
}


svg{
    width: 15px;
    height: 15px;
    overflow: visible;
    margin: 0 8px;
}
h2{
    font-size: 13px;
    padding: 5px 10px;
}
}
.slider{
    display: flex;
    flex-direction: column;
    align-content: center;
   
}


.vectorNext{
    height: 30px;
    width: 30px;
    position: absolute;
    rotate: 90deg;
    margin-left: 0px;
    align-self: self-end;
    cursor: pointer;
    margin-top: 90px;
}
.vectorPrev{
    height: 30px;
    width: 30px;
    position: absolute;
    rotate: -90deg;
    margin-left: 0px;
    align-self: flex-start;
    cursor: pointer;
}
.bodyError{
    background-color: #E5E5E5;
}   

.blocError {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.blocError p{
    font-size: 36px;
    padding: 0 20px;
}
strong {
    font-size: 288px;
    font-weight: 700;
    margin-bottom: 20px;
    color: #FF6060;

}
.bodyError p{
    font-size: 36px;
}
.blocError a {
    padding: 70px;
    font-size: 18px;
    text-decoration: underline;
}

@media screen and (max-width: 829px){
    strong{
        font-size: 96px;
    }
    .blocError p{
        font-size: 18px;
        text-align: center;
    }
    .blocError a{
        font-size: 14px;
    }
}
